@import url(https://fonts.googleapis.com/css2?family=Gochi+Hand&display=swap);
.header {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
  background-color: rgb(185, 218, 119);
  border-bottom: 2px solid #75454A;
}

.header h1 {
  color: rgb(197, 19, 19);
}

@media only screen and (max-width: 900px) {
  .header h1 {
    font-size: 2rem;
    word-wrap: break-word
  }
}
.scoreboard {
  display: flex;
  justify-content: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.score-container {
  padding: 1rem;
  margin-top: 1rem;
  background-color: beige;
  border: 2px solid #75454A;
  border-radius: 10px;
  margin-left: 1rem;
  margin-right: 1rem;
}

.score-container p {
  font-size: 1.8rem;
}
.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0.7rem;
  background-color: beige;
  border: 2px solid #75454A;
  border-radius: 10px;
  margin-left: 0.4rem;
  margin-right: 0.4rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  box-shadow: 1px 2px #61393d;
}

.card:hover {
  cursor: pointer;
  transform: scale(1.05);
  box-shadow: 2px 3px #61393d;
}

.card .img-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 220px;
  height: 220px;
  pointer-events: none;
}

.img-container img {
  max-width: 80px;
  height: auto;
}

.card .title-container {
  text-align: center;
  border-top: 2px solid #75454A;
  pointer-events: none;
}

.title-container p {
  font-size: 2rem;
  color: firebrick;
  padding-top: 0.7rem;
}
.game-board {
  margin-top: 1.5rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.game-over {
  width: 20%;
  margin-top: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  background-color: beige;
  border: 2px solid #75454A;
  border-radius: 10px;
}

.ending-text-container p {
  color: black;
  font-size: 2rem;
}

.game-over * {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.game-over .ending-text-container {
  padding: 1rem;
  text-align: center;
}

.game-over button {
  cursor: pointer;
  font-size: 1.6rem;
  padding: 0.7rem;
  color: white;
  background-color: rgb(247, 83, 34);
  border: 2px solid #75454A;
  border-radius: 0.8rem;
}

.game-over button:hover {
  background-color: rgb(248, 69, 15)
}
.memory-game {
  margin-top: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
html {
  font-size: 16px;
}

body {
  background-color: #c7daec;
}

h1 {
  text-align: center;
  font-weight: bold;
  font-size: 4rem;
}

h1,
p,
button {
  font-family: 'Gochi Hand', cursive;
}

p {
  font-size: 1.3rem;
}
